// src/router/index.js
import { createRouter, createWebHistory } from 'vue-router';
import HomePage from '../views/HomePage.vue';
import VendorPage from '../views/VendorPage.vue';
import VendorCreate from '../views/VendorCreate.vue';
import VendorModify from '../views/VendorModify.vue';
import CustmorPage from '../views/CustmorPage.vue';
import CustmorCreate from '../views/CustmorCreate.vue';
import CustmorModify from '../views/CustmorModify.vue';
import PoCreate from '@/views/PoCreate.vue';
import PoModify from '@/views/PoModify.vue';
import PoPost from '@/views/PoPost.vue';
import SoProcess from '@/views/SoProcess.vue';



// 定义路由
const routes = [
  {
    path: '/',
    name: 'HomePage',
    component: HomePage,
  },
  {
    path: '/VendorPage',
    name: 'VendorPage',
    component: VendorPage,
  },
  {
    path: '/VendorCreate',
    name: 'VendorCreate',
    component: VendorCreate,
  },
  {
    path: '/VendorModify',
    name: 'VendorModify',
    component: VendorModify,
  },
  {
    path: '/CustmorPage',
    name: 'CustmorPage',
    component: CustmorPage,
  },
  {
    path: '/CustmorCreate',
    name: 'CustmorCreate',
    component: CustmorCreate,
  },
  {
    path: '/CustmorModify',
    name: 'CustmorModify',
    component: CustmorModify,
  },
  {
    path: '/PoCreate',
    name: 'PoCreate',
    component: PoCreate,
  },
  {
    path: '/PoModify',
    name: 'PoModify',
    component: PoModify,
  },
  {
    path: '/PoPost',
    name: 'PoPost',
    component: PoPost,
  },
  {
    path: '/SoProcess',
    name: 'SoProcess',
    component: SoProcess,
  },
  
  // 添加更多路由
];

// 创建路由实例
const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
